/* Tabs Navigation */
.atblocks-tabs-navigation {
  display: flex;
  border-top: 1px solid #ddd;
}
.atblocks-tabs-navigation svg {
  fill: none;
}
.atblocks-tabs-navigation__item {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 5px;
  flex: 1;
  height: 90px;
  background-color: #f5f5f5;
  border-top: 2px solid transparent;
  border-right: 1px solid #ddd;
  outline: none !important;
  box-shadow: none !important;
}
.atblocks-tabs-navigation__item svg {
  width: 20px;
  stroke: var(--atb-color-grey, #555);
}
.atblocks-tabs-navigation__item:last-child {
  border-right: none;
}
.atblocks-tabs-navigation__item.is-active {
  background-color: #FFF;
  border-top: 2px solid var(--atb-color-primary, #335EEA);
}
.atblocks-tabs-navigation__item.is-active svg {
  stroke: var(--atb-color-primary, #335EEA);
}
.atblocks-tabs-navigation__item[data-tab=style] svg {
  stroke: none;
  fill: var(--atb-color-grey, #555);
}
.atblocks-tabs-navigation__item[data-tab=style].is-active svg {
  stroke: none;
  fill: var(--atb-color-primary, #335EEA);
}
.atblocks-tabs-navigation__item-label {
  color: var(--atb-color-grey, #555);
  transition: color 250ms ease;
}

/* Component Header */
.atblocks-component-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px 0;
}
.atblocks-component-header__title {
  margin-right: 10px;
}
.atblocks-component-header .atblocks-component-device-switcher {
  margin-right: auto;
}